@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;1,100;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;700&display=swap);
header {
  background-color: #fff;
  height: 70px;
  align-items: center;
  color: #121212;
  border: 1px solid #e8e8e8;
}

.header-section {
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  width: 90%;
  margin: 0 auto;
}

#logo {
  color: #0290ff;
  font-size: 30px;
}

.nav-bar {
  flex-grow: 4;
}

.nav-bar > li {
  padding: 0 10px;
}

.login-icon {
  width: 25px;
  height: 25px;
  background: url(/bookStore/static/media/log.2eb6c618.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.new-book-wrapper {
  padding: 20px 0;
  width: 90%;
  margin: 0 auto;
}

.new-book-wrapper h2 {
  color: #888;
  padding-bottom: 15px;
}

.newBookTitle,
.newBookAuthor,
.newBookCategory {
  background-color: #fff;
  border: 1px solid #e8e8e8;
  height: 30px;
}

form {
  grid-gap: 10px;
  gap: 10px;
}

.newBookTitle,
.newBookAuthor {
  flex-grow: 2;
}

.newBookCategory,
.submitBtn {
  flex-grow: 1;
}

.bookWrapper {
  padding: 10px 20px;
  width: 90%;
  margin: 0 auto;
  align-items: center;
  background-color: #fff;
  border: 1px solid #e8e8e8;
}

.ulBooks > li {
  margin: 15px 0;
}

.book-info-wrapper {
  flex-grow: 2;
  max-width: 45%;
}

.book-info {
  padding-bottom: 15px;
}

.book-category {
  color: gray;
  font-size: 13px;
  letter-spacing: 0.3px;
}

.book-title,
.current-chapter {
  word-break: break-word;
}

.current-chapter {
  color: gray;
  font-weight: 50;
  font-size: 15px;
  text-transform: capitalize;
  margin-bottom: 15px;
}

.book-author,
.options-ul,
.options-ul > li > button {
  color: #4386bf;
  font-weight: 100;
  font-size: 12px;
  opacity: 0.8;
}

.current-chapter-wrapper,
.completion-wrapper {
  flex-grow: 1;
}

.completion-wrapper {
  align-items: center;
}

.background-wrapper {
  padding: 0 10px;
}

.current-chapter-wrapper > * {
  display: block;
  padding: 2px 0;
}

.current-chapter-wrapper p {
  text-transform: uppercase;
}

.current-chapter-wrapper {
  max-width: 25%;
}

.options-ul > li {
  padding-right: 10px;
}

.btn {
  border: none;
  padding: 0;
  outline: none;
  background-color: #fff;
}

.completed {
  color: #121212;
  font-size: 30px;
}

.completed-p,
.current-chapter-wrapper p {
  color: gray;
  font-size: 12px;
  opacity: 0.6;
}

.progress {
  background: url(/bookStore/static/media/progressCircle.5006fc50.png);
  background-size: contain;
  background-repeat: no-repeat;
  width: 100px;
  height: 100px;
}

.update-progress {
  background-color: #0290ff;
  color: rgba(255, 255, 255, 0.535);
  padding: 3px 15px;
  border-radius: 2px;
  border: transparent;
}

.submitBtn {
  background-color: #0290ff;
  color: #fff;
  border: transparent;
  padding: 10px;
  border-radius: 2px;
  font-size: 10px;
  text-transform: uppercase;
}

/*
    Global styles only
*/

#root {
  background-color: #fafafa;
  min-height: 100vh;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a,
li {
  text-decoration: none;
  list-style-type: none;
}

a:visited {
  color: inherit;
}

.flex {
  display: flex;
}

.book-category,
.completed,
.completed-p {
  font-family: 'Montserrat', sans-serif;
}

.book-title,
.book-author,
.options-ul > li,
.current-chapter,
.update-progress {
  font-family: 'Roboto Slab', serif;
}

